@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
.container {
    position:relative;
    width: 607px;
    height: 829px;
    margin: auto;
    margin-top: 50px;
    border-radius: 12px;
    background: linear-gradient(180deg, #130754 0%, #3b2f80 100%);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  }

  .hot {
    background: linear-gradient(180deg, #800d61 0%, #fa5039 100%);
  }

  .cool {
    background: linear-gradient(180deg, #122159 0%, #9661a1 100%);
  }

  .mild {
    background: linear-gradient(180deg, #4fa4db 0%, #73bae1 100%);
  }

  .warm {
    background: linear-gradient(180deg, #3e63a4 0%, #c9aaa3 100%);
  }

  .title {
    display: flex;
    justify-content: left;
    color: #fff;
    font-weight:100;
    font-size: 40px;
    padding-top:40px;
    padding-left:60px;
}

  .top-bar {
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 25px;
  }

  .top-bar input {
    display: flex;
    width: 362px;
    height: 50px;
    background: #f7f7f7;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;
  }
  .top-bar .search-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 50px;
    background: #f7f7f7;
    border-radius: 40px;
    cursor: pointer;
  }
  .weather-image {
    margin-top: 35px;
    display: flex;
    justify-content: center;
  }

  .weather-temp {
    display: flex;
    justify-content: center;
    color: #fff;
    font-size: 120px;
    font-weight: 400;
  }

  .weather-location {
    display: flex;
    justify-content: center;
    color: #fff;
    font-size: 60px;
    font-weight: 400;
  }

  .weather-feels-like {
    display: flex;
    justify-content: center;
    color: #fff;
    font-weight:100;
    font-size: 40px;
    padding-top:40px;
}

  .error {
    display: flex;
    justify-content: center;
    color: #fff;
    font-size: 40px;
    font-weight: 400;
    margin-top:20px;
}

.authorname {
    position:absolute;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
    width:max-content;
    bottom:25px;
    display:flex;
    justify-content: center;
    text-align:center;

    color:#fff;
}


  .data-container {
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
  }
  
  .element {
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
  }
  
  .data {
    font-size: 34px;
    font-weight: 400;
  }

  .text {
    font-size: 20px;
    font-weight: 400;
  }
